
import { defineComponent, inject } from "vue";

export default defineComponent({
  name: "ModelTreeItemComponent",
  setup(prop) {
    // Inject Data
    const nodeSelectionStates = inject<{ [key: number]: boolean }>(
      "nodeSelectionStates"
    );
    if (nodeSelectionStates) {
      nodeSelectionStates[prop.nodeId] = false;
    }
    return { nodeSelectionStates };
  },
  props: {
    nodeId: {
      type: Number,
      required: true,
    },
    level: {
      type: Number,
      required: true,
    },
    hwv: {
      type: Communicator.WebViewer,
      required: true,
    },
  },
  data() {
    return {
      nodeName: "",
      childrenIds: [] as number[],
      isCollapsed: false,
    };
  },
  computed: {
    isSelected(): boolean {
      if (!this.nodeSelectionStates || !this.nodeSelectionStates[this.nodeId]) {
        return false;
      }
      return this.nodeSelectionStates[this.nodeId];
    },
  },
  created() {
    // Init data()
    switch (this.hwv.model.getNodeType(this.nodeId)) {
      case Communicator.NodeType.Part:
      case Communicator.NodeType.PartInstance:
      case Communicator.NodeType.BodyInstance:
      case Communicator.NodeType.AssemblyNode: {
        let nName = this.hwv.model.getNodeName(this.nodeId);
        this.nodeName = nName ? nName : "";
        this.childrenIds = this.hwv.model.getNodeChildren(this.nodeId);
        break;
      }
      default:
        break;
    }
  },
  methods: {
    onSelectClick() {
      // Select the corresponding part in the viewer
      this.hwv.selectPart(this.isSelected ? null : this.nodeId);
    },
    onCollapseClick() {
      this.isCollapsed = !this.isCollapsed;
    },
  },
});
