
import ViewerComponent from "./components/viewer-component.vue";
import ModelTreeComponent from "./components/model-tree-component.vue";
import { defineComponent } from "vue";
import { CameraStatus } from "./data/camera-status";

export default defineComponent({
  name: "App",
  components: {
    ViewerComponent,
    ModelTreeComponent,
  },
  data() {
    return {
      // Attach base url so the path will always work
      modelPath: process.env.BASE_URL + "assets/drive_assembly.scs",
      imgPath: process.env.BASE_URL + "assets/ts3d_logo.png",
      cameraStatus: undefined as CameraStatus | undefined,
      isStructureReady: false,
      currentTab: 1, // 1: Home, 2: ModelTree
      operator: "Orbit",
      hwv: undefined as Communicator.WebViewer | undefined,
    };
  },
  methods: {
    // Callback for when the web viewer is ready
    hwvReady(hwv: Communicator.WebViewer): void {
      hwv.setCallbacks({
        sceneReady: () => {
          this.cameraStatus = hwv.view.getCamera().toJson() as CameraStatus;
        },
        modelStructureReady: () => {
          this.isStructureReady = true;
        },
        camera: () => {
          this.cameraStatus = hwv.view.getCamera().toJson() as CameraStatus;
        },
      });
      this.hwv = hwv;
    },
    // Methods for event handeling
    changeTab(newTab: number) {
      this.currentTab = newTab;
    },
    changeOperator(event: Event) {
      if (!this.hwv) return;
      this.hwv.operatorManager.clear();
      this.hwv.operatorManager.push(Communicator.OperatorId.Orbit);
      let newOperator = (event.target as HTMLSelectElement).value;
      if (newOperator === "Area Select") {
        this.hwv.operatorManager.push(Communicator.OperatorId.AreaSelect);
      } else if (newOperator === "Select") {
        this.hwv.operatorManager.push(Communicator.OperatorId.Select);
      } else if (newOperator === "Measure") {
        this.hwv.operatorManager.push(
          Communicator.OperatorId.MeasurePointPointDistance
        );
      }
    },
  },
});
